/* all the styles defined for used modules */
html {
    font-family: TEXT_CONTENT, TEXT_CONTENT_SYNTH, 'Microsoft YaHei', 'Source Han Serif SC', serif;
}

body {
    margin: 0;
    font-size: 1em;
    line-height: 1.5em;
    -webkit-font-smoothing: antialiased;
}

h1,
h2,
h3 {
    font-family: "TEXT_CONTENT_CAPTION" !important;
    font-weight: 600;
    color: $heading-font-color;
    font-family: $heading-font;
    line-height: 1.5;
}

h1 {
    color: $main;
    font-size: 2em;

    a,
    a:visited {
        text-decoration: none;
        color: $main;
    }
}

h2 {
    font-size: 1.5em;
}

a,
a:visited {
    color: $lay_grayblack;
}

header {
    background: url(../static/img/Hypergraph_sizel.jpg);
    padding: 5em;
    text-align: center;
    margin: 0em;
    height: 40em;
    background-size: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.5;
}

main {
    padding: 0 15px;
    max-width: $content-width;
    margin: 0 auto;
}

time {
    color: #898989;
}

.container {
    max-width: $content-width;
    margin: 0 auto;
}

footer {
    position: fixed;
    background-color: $lay_deepgrayblack;
    font-size: 0.75rem;
    color: $light;
    text-align: center;
    padding: 0.4em;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: $top_z_index;
    height: 3em;
    a,
    a:visited {
        color: $light;
        text-decoration: none;
    }
}

#navigation_bartop {
    border: 1px solid $lay_transblack;
    position: fixed;
    height: 3.5em;
    top: 0;
    right: 0;
    left: 0;
    z-index: $top_z_index;
    background: $lay_transwhite;
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
    vertical-align: middle;

    nav {
        ul {
            font-size: 1em;
            margin: 1em;
            flex-wrap: nowrap !important;
            display: flex;
            flex-direction: row;
            justify-content: center;
            list-style: none;

            li {
                float: right;
                position: relative;

                #name_bartop {
                    font-size: 1.25em;
                    flex-wrap: nowrap !important;
                    padding-right: 1.5rem;
                    padding-left: 0rem;
                }

                a {
                    font-size: 1rem;
                    text-decoration: none;
                    display: block;
                    padding-right: .5rem;
                    padding-left: .5rem;
                    font-family: "TEXT_CONTENT_CAPTION";
                    line-height: 1.5em;
                    position: relative;

                    &:after {
                        content: '';
                        position: absolute;
                        width: 100%;
                        transform: scaleX(0);
                        height: 0.1em;
                        bottom: 0;
                        left: 0;
                        background-color: $lay_grayblack;
                        transform-origin: bottom right;
                        transition: transform $transition_time ease-in;
                    }

                    &:hover:after {
                        transform: scaleX(1);
                        transform-origin: bottom left;
                    }
                }
            }
        }
    }
}

#scroll-progress {
    margin-top: 3.5em;
    position: fixed;
    top: 0;
    left: 0;
    width: 0%;
    height: 0.15em;
    background-color: $lay_grayblack;
    opacity: 1;
    z-index: $top_z_index;
}

.logo {
    width: 100px;
    height: 100px;
    filter: none;
    cursor: pointer;
    border-radius: 50%;
    flex-shrink: 0;
    margin-right: 10px;
    border: .08em solid #ffffff !important;
    user-select: none;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
    -webkit-transition: -webkit-transform 1s cubic-bezier(0.65, 0.05, 0.36, 1);
    -moz-transition: -moz-transform 1s cubic-bezier(0.65, 0.05, 0.36, 1);
    -o-transition: -o-transform 1s cubic-bezier(0.65, 0.05, 0.36, 1);
    -ms-transition: -ms-transform 1s cubic-bezier(0.65, 0.05, 0.36, 1);
}

.logo:hover {
    -webkit-transform: rotateZ(360deg);
    -moz-transform: rotateZ(360deg);
    -o-transform: rotateZ(360deg);
    -ms-transform: rotateZ(360deg);
    transform: rotateZ(360deg);
}