/* all variables, mixins, and resets are defined */

$content-width: 800px;
$main: #19ccaa;
$lay_palevioletred: #db7093;
$lay_seagreen: #2e8b57;
$lay_grayblack: #282828;
$lay_deepgrayblack: #1c1c1d;
$lay_tomato: #ff6347;
$lay_slateblue: #6a5acd;
$lay_transwhite: #ffffffa0;
$lay_transblack: #00000020;
$heading-font: 'Muli', sans-serif;
$heading-font-color: #425469;
$light: #e7edf4;
$header_background: url("../static/img/Hypergraph_sizel.jpg");
$top_z_index: 1032;
$transition_time: 0.3s;

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

